<template>
  <div class="upload-page">
    <div class="title">上传图片，可多选
      <div class="tips">（上传文件只能是 png、jpg、jpeg、gif格式，单个文件大小上不能超过 2MB，图片宽高均不超过4000PX,宽高比不超过1:25）</div>
    </div>
    <div class="folder">目录：https://resource.aijiatui.com/<el-input v-model="imgFolder" class="folder-input"></el-input></div>
    <ul class="multi-box">
      <li v-for="(item, index) in uploadImgList" :key="index">
        <img :src="item">
        <!-- <span class="name">{{item}}</span> -->
      </li>
      <Multiple-Upload-Img
        style="float: left"
        @upload-img-return-url="uploadImgReturnUrl"
        :maxLength='10'
        :folder="imgFolder"
        ref="multipleUploadImg">
        <li class="cicle_border">
          <i class="el-icon-plus"></i>
        </li>
      </Multiple-Upload-Img>
    </ul>

    <div class="title mt200">上传视频，单选
      <div class="tips">（上传文件只能是 MP4格式!，单个文件大小不能超过 5MB）</div>
    </div>
    <div class="folder">目录：https://resource.aijiatui.com/<el-input v-model="videoFolder" class="folder-input"></el-input></div>
    <Single-Upload-File
      :width="140"
      :height="140"
      :folder="videoFolder"
      :filePath="videoPath"
      :fileType="2"
      :maxSize="5"
      @update-file="updateVideo"></Single-Upload-File>
  </div>
</template>

<script>
import MultipleUploadImg from '@/components/com/MultipleUploadImg'
import SingleUploadFile from "@/components/com/SingleUploadFile"
import api from '@/api'
import { mapGetters } from 'vuex';

export default {
  components: {
    MultipleUploadImg,
    SingleUploadFile
  },
  watch: {

  },
  data() {
    return {
        uploadImgList: [],
        imgFolder: 'superip/images/',
        videoFolder: 'superip/video/',
        videoPath: ''
    };
  },
  computed: {

  },
  mounted() {

  },
  methods: {
    uploadImgReturnUrl(imgurl){
      this.uploadImgList.push(imgurl);
    },
    updateVideo(url) {
      this.videoPath = url;
    }
  }
};
</script>

<style lang="scss" scoped>
.upload-page{
  margin: 50px 100px;
  .title{
    font-size: 22px;
    color: #409eff;
    margin-bottom: 40px;
    .tips{
      font-size: 14px;
    }
  }
  // border: 1px solid;
  .folder-input{
    width: 300px;
    margin-left: 20px;
  }
  .folder{
    margin-bottom: 10px;
  }
  .multi-box{
    overflow: hidden;
    li{
      width: 140px;
      height: 140px;
      float: left;
      margin-right: 10px;
      img{
        width: 100%;
        height: 100%;
        border: 1px solid #409eff;
      }
      .name{
        display: block;
        margin-top: 5px;
      }
      &.cicle_border {
        border: 1px dotted #409eff;
        border-radius: 5px;
        text-align: center;
        line-height: 140px;
        font-size: 30px;
        cursor: pointer;
      }
    }
  }
  .mt200{
    margin-top: 200px;
  }
}

  
</style>



